import { usersAPI } from "../api/api";

const SET_FRIENDS = 'SET_FRIENDS';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';

let initialState = {
    friends: [],
    isFetching: false
}

const friendsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_FRIENDS:
            return {
                ...state,
                friends: action.friends
            }
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            }
        default:
            return state;
    }
}

export const setFriends = (friends) => ({
    type: SET_FRIENDS,
    friends
})
export const toggleIsFetching = (isFetching) => ({
    type: TOGGLE_IS_FETCHING,
    isFetching
})

export const getFriends = () => async (dispatch) => {
    dispatch(toggleIsFetching(true));
    let response = await usersAPI.getUsers(1, 10);
    let totalUsersCount = response.totalCount;
    let pagesCount = Math.ceil(totalUsersCount / 100);
    let friends = [];
    for (let i = 1; i <= pagesCount; i++){
        let response = await usersAPI.getUsers(i, 100);
        friends = [ ...friends, ...response.items.filter(f => f.followed === true)];
    }
    dispatch(setFriends(friends));
    dispatch(toggleIsFetching(false));
}

export default friendsReducer;