import React from 'react';
import classes from './Friends.module.css';
import DefaultIcon from './../../icons/default_icon_user.png';
import { NavLink } from 'react-router-dom';

let Friend = ({ friend, followingInProcess, followOrUnfollow }) => {
    return (
        <div>
            <div className={classes.avaAndFollowField}>
                <NavLink to={'/profile/' + friend.id}>
                    <img src={friend.photos.small != null ? friend.photos.small : DefaultIcon} className={classes.ava} />
                </NavLink >
                <div className={classes.areaForButton}>
                    {friend.followed
                        ? <button disabled={followingInProcess.some(id => id === friend.id)} onClick={() => {
                            followOrUnfollow(friend.id, false)
                        }} className={classes.forButton}>Unfollow</button>
                        : <button disabled={followingInProcess.some(id => id === friend.id)} onClick={() => {
                            followOrUnfollow(friend.id, true)
                        }} className={classes.forButton}>Follow</button>
                    }
                </div>
            </div>
            <NavLink to={'/profile/' + friend.id}>
                <div className={classes.item}>
                    <div className={classes.userData}>
                        <div className={classes.userName}>{friend.name}</div>
                        {friend.status && <div className={classes.userStatus}>{friend.status}</div>}
                    </div>
                </div>
            </NavLink >
        </div>
    )
}
export default Friend;