import React from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { WithAuthRedirect } from '../HOC/WithAuthRedirect';
import Friends from './Friends';
import { getFriends } from '../../redux/friendsReducer';
import { getFollowingInProcess } from '../../redux/usersCelectors';
import { followOrUnfollow } from '../../redux/usersReducer';

class FriendsContainer extends React.Component {
    componentDidMount() {
        this.props.getFriends();
    }

    componentDidUpdate(prevProps) {
        if(this.props.friends.length !== prevProps.friends.length) {
            this.props.getFriends();
        }
    }

    render() {
        return (
            <Friends friends={this.props.friends} isFetching={this.props.isFetching}
                followingInProcess={this.props.followingInProcess}
                followOrUnfollow={this.props.followOrUnfollow} />
        );
    }
}

let mapStateToProps = (state) => {
    return {
        friends: state.friendsPage.friends,
        isFetching: state.friendsPage.isFetching,
        followingInProcess: getFollowingInProcess(state),
    }
}

export default compose(
    connect(mapStateToProps, {
        getFriends, followOrUnfollow
    }),
    WithAuthRedirect
)(FriendsContainer)